.stages {
  padding: 150px 0;

  @include mobile {
      padding: 100px 0;
  }

  &__title {
    font-weight: 800;
    font-size: 48px;
    line-height: 59px;
    text-align: center;
    margin-bottom: 100px;

    @include mobile {
        font-size: 36px;
line-height: 44px;
margin-bottom: 50px;
    }

    @include small-mobile {
        font-size: 32px;
        line-height: 36px;
    }
  }

  &-content {
    &__title {
      font-weight: 800;
      font-size: 36px;
      line-height: 44px;
      margin-bottom: 50px;
      @include small-mobile {
          font-size: 28px;
          line-height: 34px;
      }
    }

    &__list {
      display: flex;
      flex-direction: column;
      gap: 50px;
      list-style: none;
    }
  }

  &-item {
    display: flex;
    align-items: center;
    max-width: 1105px;

    &__text {
      font-weight: bold;
      font-size: 24px;
      line-height: 29px;

      @include mobile {
        font-size: 18px;
        line-height: 22px;
      }
    }

    &__count {
        font-family: "Roboto", "Montserrat", sans-serif;
      font-weight: bold;
      font-size: 48px;
      min-width: 80px;
      max-width: 80px;
      width: 80px;
      height: 80px;
      border: 1px solid #FFFFFF;
      border-radius: 50%;
      margin-right: 60px;
      line-height: 80px;
      text-align: center;

      @include mobile {
          margin-right: 40px;
      }

      @include small-mobile {
      font-size: 26px;
      min-width: 45px;
      max-width: 45px;
      width: 45px;
      height: 45px;
      line-height: 45px;
      margin-right: 20px;
      }
    }
  }
}
