.questions {
  padding: 200px 0 70px;
  background: url('../img/questions-bg.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;

  @include mobile {
    padding: 175px 0 70px;
  }

  
  @include small-mobile {
      padding: 130px 0 70px;
}

  &::after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, rgba(11, 29, 51, 0) 0%, rgba(11, 29, 51, 0.8) 0.03%, rgba(11, 29, 51, 0) 100%);

    @include mobile {
        background: rgba(11, 29, 51, 0.5);
    }
  }

  &__container {
    position: relative;
    z-index: 10;
  }

  &__content {
    margin-bottom: 145px;
    
    @include small-mobile {
        margin-bottom: 100px;
    }
  }

  &__title {
    font-weight: 300;
    font-size: 64px;
    line-height: 78px;
    margin-bottom: 35px;
    text-transform: uppercase;

    @include tablet {
        font-size: 54px;
        line-height: 60px;
    }

    @include mobile {
      font-weight: 300;
      font-size: 48px;
      line-height: 59px;
      text-align: center;
      margin-bottom: 10px;
    }

    
    @include small-mobile {
        font-size: 32px;
        line-height: 46px;
    }

    span {
      color: $mainColor;
      font-weight: 700;
    }
  }

  &__subtitle {
    font-size: 36px;
    line-height: 150%;
    color: rgba($whiteColor, 0.7);
    text-transform: uppercase;
    max-width: 830px;
    margin-bottom: 100px;

    @include mobile {
      font-size: 24px;
      text-align: center;
    }

    @include small-mobile {
        font-size: 18px;
        margin-bottom: 60px;
    }

    span {
      font-weight: 700;
    }
  }

  &__button {
    font-weight: normal;
    font-size: 24px;
    line-height: 29px;
    padding: 35px 55px;
    background: $mainColor;
    display: block;
    max-width: 430px;
    text-align: center;

    @include mobile {
      font-size: 18px;
      line-height: 22px;
      padding: 28px 40px;
      max-width: 325px;
      margin: 0 auto;
    }

    @include small-mobile {
        padding: 20px 16px;
    }
  }

  &-benefits {
    display: flex;
    align-items: center;

    @include small-mobile {
        flex-direction: column;
        align-items: start;
    }

    &__img {
      display: block;
      margin-right: 30px;

      img {
        border-radius: 10px;
        max-width: 228px;
        max-height: 135px;
        object-fit: cover;

        @include small-mobile {
            max-width: 175px;
            max-height: 104px;
            margin-bottom: 15px;
        }
      }
    }

    &__text {
      max-width: 540px;
      font-weight: 500;
      font-size: 21px;
      line-height: 30px;
      text-transform: uppercase;
      color: rgba($whiteColor, 0.7);

      @include mobile {
        font-size: 14px;
        line-height: 20px;
      }
    }
  }
}
