.income {
  padding: 150px 0;

  @include mobile {
    padding: 100px 0;
  }

  @include small-mobile {
    padding: 70px 0;
  }

  &__title {
    font-weight: bold;
    font-size: 48px;
    line-height: 170%;
    letter-spacing: 0.15em;
    margin-bottom: 25px;

    @include mobile {
      font-size: 36px;
      text-align: center;
    }

    @include small-mobile {
      font-size: 23px;
    }

    span {
      color: $mainColor;
    }
  }

  &__remark {
    margin-bottom: 50px;
    font-size: 24px;
    line-height: 180%;
    letter-spacing: 0.15em;

    @include mobile {
      font-size: 18px;
      text-align: center;
    }

    @include small-mobile {
      font-size: 16px;
    }
  }

  &__content {
    padding: 50px 100px;
    background: #0E233E;
    border-radius: 12px;

    @include tablet {
      padding: 50px 60px;
    }

    @include mobile {
      padding: 50px 40px;
    }
  }

  &-content {
    max-width: 1300px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include mobile {
      flex-direction: column;
    }

    &__icon {
      width: 80px;
      height: 80px;
      margin-right: 100px;

      svg {
        object-fit: cover;
        width: 100%;
        height: 100%;
      }

      @include tablet {
        margin-right: 40px;
      }

      @include mobile {
        margin-right: 0;
        margin-bottom: 50px;

        width: 60px;
        height: 60px;
      }

      @include small-mobile {
        width: 40px;
        height: 40px;
        margin-bottom: 30px;
      }
    }

    &__text {
      font-size: 24px;
      line-height: 29px;

      @include mobile {
        font-size: 18px;
        line-height: 22px;
        text-align: center;
      }
    }
  }
}
