.credit {
  padding: 170px 0 330px;
  background: url('../img/credit-bg.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;

  @include mobile {
    padding: 100px 0;
  }

  @include small-mobile {
      padding: 70px 0;
  }

  &::after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));

    @include mobile {
      background: rgba(11, 29, 51, 0.5);
    }
  }

  &__container {
    position: relative;
    z-index: 10;
  }

  &__title {
    font-weight: 700;
    font-size: 48px;
    line-height: 143%;
    letter-spacing: 0.15em;
    margin-bottom: 25px;
    max-width: 1400px;

    @include mobile {
      font-size: 36px;
      text-align: center;
    }

    @include small-mobile {
      font-size: 24px;
    }
  }

  &__text {
    font-weight: 500;
    font-size: 24px;
    line-height: 143%;
    letter-spacing: 0.15em;
    max-width: 1300px;
    margin-bottom: 150px;

    @include mobile {
      font-size: 18px;
      text-align: center;
      margin-bottom: 100px;
    }

    @include small-mobile {
      font-size: 16px;
      margin-bottom: 60px;
    }

    p {
      &:first-child {
        margin-bottom: 25px;
      }
    }
  }

  &__buttons {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 50px;

    @include mobile {
      flex-direction: column;
      gap: 25px;
    }
  }

  &__button {
    max-width: 600px;
    padding: 32px 50px;
    font-weight: normal;
    font-size: 30px;
    line-height: 37px;

    @include mobile {
      font-size: 24px;
      line-height: 29px;
    }

    @media (max-width: 600px) {
        padding: 18px 15px;
        font-size: 16px;
        line-height: 22px;
    }
    @media (max-width: 355px) {
        padding: 16px 10px;
        font-size: 15px;
        line-height: 22px;
    }

    &:first-child {
      background: $mainColor;

      @include mobile {
      }
    }
  }
}
