.advantages {
  padding: 75px 0 130px;
  background: url('../img/advantages-bg.jpg') #061323;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;

  @include mobile {
    padding: 50px 0 100px;
  }

  &::after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    background: rgba(11, 29, 51, 0.7);

    @include mobile {
      background: rgba(11, 29, 51, 0.5);
    }
  }

  &__container {
    position: relative;
    z-index: 10;
  }

  &__title {
    font-weight: 600;
    font-size: 64px;
    line-height: 78px;
    text-transform: uppercase;
    margin-bottom: 100px;
    max-width: 1250px;

    span {
      color: $mainColor;
    }

    @include mobile {
      text-align: center;
      font-size: 36px;
      line-height: 44px;
      margin-bottom: 50px;
    }

    @include small-mobile {
      font-size: 28px;
      line-height: 32px;
    }
  }

  &__items {
    max-width: 1300px;
    display: flex;
    flex-direction: column;
    margin-bottom: 70px;
    list-style: none;
  }

  &__item {
    font-weight: normal;
    font-size: 36px;
    line-height: 44px;
    position: relative;
    padding-left: 80px;

    @include mobile {
      font-size: 18px;
      line-height: 22px;
      padding-left: 60px;
    }

    @include small-mobile {
      font-size: 16px;
      padding-left: 40px;
    }

    &:not(:last-child) {
      margin-bottom: 30px;
    }

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      background: url('../img/check-icon.png');
      background-repeat: no-repeat;
      background-size: cover;
      width: 50px;
      height: 50px;

      @include mobile {
        width: 40px;
        height: 40px;
      }

      @include small-mobile {
        width: 30px;
        height: 30px;
      }
    }
  }

  &__question {
    display: block;
    font-weight: normal;
    font-size: 36px;
    line-height: 44px;
    text-transform: uppercase;
    padding: 30px 50px;
    max-width: 430px;
    background: $mainColor;
    text-align: center;

    @include mobile {
      font-size: 24px;
      line-height: 29px;
      padding: 20px 35px;
      max-width: 360px;
    }
  }
}
