.footer {
  background: #000;
  padding: 80px 0;

  @include mobile {
      padding: 50px 0;
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include tablet {
        flex-wrap: wrap;
        gap: 40px;
    }
  }

  &__item {
      margin: 0 auto;
  }

  &__logo {
    font-weight: bold;
    font-size: 36px;
    line-height: 44px;
    @include tablet {
        font-size: 28px;
        line-height: 32px;
    }
  }

  &__policy {
    font-weight: bold;
    font-size: 24px;
    line-height: 29px;
    color: rgba(255, 255, 255, 0.5);
    
    @include big-tablet {
        font-size: 20px;
        line-height: 24px;
    }

    
    @include small-mobile {
        font-size: 18px;
        line-height: 20px;
    }
  }

  &__call {
    padding: 25px 40px;
    text-align: center;
    background: $mainColor;
    max-width: 325px;
    width: 100%;

    @include big-tablet {
        max-width: 225px;
    }
  }

  &__number {
    font-weight: bold;
    font-size: 24px;
    line-height: 29px;

    @include tablet {
        font-size: 20px;
        line-height: 24px;
    }
    
    @include small-mobile {
        font-size: 18px;
        line-height: 20px;
    }
  }
}
