.country {
  padding: 150px 0;

  @include mobile {
      padding: 100px 0;
  }

  &__title {
    position: relative;
    font-weight: 600;
    font-size: 64px;
    line-height: 78px;
    text-transform: uppercase;
    margin-bottom: 100px;

    @include mobile {
        font-size: 36px;
line-height: 44px;
margin-bottom: 50px;
    }

    @include small-mobile {
      font-size: 28px;
      line-height: 32px;
    }

    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      width: 65px;
      height: 3px;
      background: #FFFFFF;

      @include mobile {
        display: none;
      }
    }
  }

  &__items {
    max-width: 80%;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    column-gap: 120px;
    row-gap: 130px;

    @include mobile {
        column-gap: 50px;
        row-gap: 40px;
        margin: 0 auto;
    }
  }

  &__item {
    font-weight: normal;
    font-size: 48px;
    line-height: 59px;
    color: rgba($whiteColor, 0.5);
    white-space: nowrap;

    @include mobile {
        font-size: 24px;
line-height: 29px;
    }
    @include small-mobile {
      font-size: 20px;
      line-height: 24px;
    }
  }
}
