.connection {
  padding: 90px 0 75px;
  background: url('../img/connection-bg.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));

  @include mobile {
      padding: 50px 0 50px;
  }

  &::after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.3));

    @include mobile {
      background: rgba(11, 29, 51, 0.5);
    }
  }

  &__container {
    position: relative;
    z-index: 10;
  }

  &__title {
    font-weight: 600;
    font-size: 48px;
    line-height: 180%;
    margin-bottom: 100px;

    @include small-mobile {
        margin-bottom: 50px;
        font-size: 36px;
        line-height: 44px;
    }
  }

  &-form {
    margin: 0 auto;
    max-width: 1048px;

    &__item {
      width: 100%;
      height: 45px;

      &:not(:last-child) {
          margin-bottom: 50px;
      }
    }

    &__button {
      padding: 32px 125px;
      max-width: 425px;
      font-weight: normal;
      font-size: 30px;
      line-height: 37px;
      background: $mainColor;
      display: block;
      margin: 70px auto 0;

      @include small-mobile {
          padding: 18px 50px;
          font-size: 24px;
          line-height: 26px;
          margin: 50px auto 0;
      }
    }

    &__label {
        display: block;
        margin: 0 auto;
      font-weight: bold;
      font-size: 18px;
      line-height: 180%;
      letter-spacing: 0.15em;
      color: rgba($whiteColor, 0.5);
      text-align: center;
    }

    &__input {
        font-family: inherit;
      background: none;
      border: none;
      border-bottom: 2px solid #fff;
      width: 100%;
      height: 45px;
      font-weight: bold;
      font-size: 18px;
      line-height: 180%;
      letter-spacing: 0.15em;
      color: rgba($whiteColor, 0.7);
      text-align: center;
      resize: none;
      outline: none;
    }
  }
}
