.location {
  &__top {
    padding: 150px 0 100px;
    @include mobile {
        padding: 100px 0 50px;
    }
  }

  &__title {
    font-weight: 600;
    font-size: 48px;
    line-height: 59px;

    @include small-mobile {
        font-size: 36px;
        line-height: 44px;
    }
  }

  &__container {
    iframe {
      width: 100%;
      height: 900px;

      @include tablet {
          height: 600px;
      }

      @include mobile {
          height: 420px;
      }
    }
  }
}

.location-bottom {
  padding: 50px 0 60px;
  display: grid;
  row-gap: 70px;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: auto;
  column-gap: 40px;

  @include mobile {
      grid-template-columns: auto;
      row-gap: 40px;
  }

  &__item {
    font-weight: bold;
    font-size: 24px;
    line-height: 29px;
    color: rgba($whiteColor, 0.6);

    @include mobile {
        font-size: 20px;
line-height: 25px;
    }
    @include small-mobile {
        font-size: 18px;
line-height: 20px;
    }
  }

  &__address {
    text-transform: uppercase;
  }
}

.location-socials {
  display: flex;
  align-items: center;

  a {
    display: block;
    width: 35px;
    height: 35px;
    @include small-mobile {
        width: 25px;
        height: 25px;
    }

    img {
      width: 100%;
      height: 100%;
    }

    &:first-child {
      margin-left: 65px;
      @include mobile {
          margin-left: 20px;
      }
    }

    &:not(:last-child) {
      margin-right: 65px;
      @include mobile {
          margin-right: 20px;
      }
    }
  }

  &__items {
    display: flex;
    align-items: center;
  }
}
